.toggle-switch {
    position: relative;
    display: inline-block;
  }

.toggle-switch label {
    width: 3rem;
    height: 1rem;
    position: relative;
    display: inline-block;
    border-radius: 46px;
    transition: 0.4s;
}
.toggle-switch label:after {
    content: '';
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    left: 0;
    top: -5px;
    z-index: 2;
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    transition: 0.4s;
}
.toggle-switch input {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    opacity: 0;
    cursor: pointer;
}
.toggle-switch input:hover + label:after {
    outline: solid 0.5rem #0134401A;
}

.toggle-switch input:active + label:after {
    outline: solid 0.5rem #01344040;
}

.toggle-switch input:checked + label:after {
    left: 1.5rem;
}

.toggle-component .toggle-switch label {
    background-color: #00000040;
    width: 3rem;
    height: 1rem;
}
.toggle-component .toggle-switch label:after {
    background-color: #ffffff;
    box-shadow: 0px 2px 4px 0px #00000040;
    top: -4px;
    height: 1.5rem;
    width: 1.5rem;
}
.toggle-component .toggle-switch input:checked + label {
    background-color: #01344066;
}
.toggle-component .toggle-switch input:checked + label:after {
    background-color: #000000fb;
    left: 1.5rem;
}
