.sub-menu-item {
    width: 22rem;
    display: flex;
    flex-direction: row;
    border: none;
    align-items: center;
    appearance: none;
    cursor: pointer;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 4rem;
    padding-right: 1.5rem;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
    color: #013440;

    &.selected {
        background-color: #D6EBED;
        font-weight:900;
        
    }

    &.normal {
        background-color: #FFFFFF;
        font-weight: 600;
    }
    &:hover{
        background-color: #C1D9DC;
        transition: .5s;
    }
}
